@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');
*{
    box-sizing: border-box;
    font-family: "Inter",sans-serif;
}
.app-container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.qrimg{
    padding: 5px;
    box-shadow:0 0 10px rgba(0, 0,0,0.1);
}
.input-label{
    display:block;
    margin-bottom: 8px;
    color:#3100b6;
    font-size: 15px;
    font-weight: 500;
}
input[type="text"]{
    padding: 12px;
    margin-bottom: 20px;
    width:100%;
    font-size: 16px;
    border:2px solid #3100b6;
    border-radius: 5px;
}
button{
    padding: 15px 20px;
    font-size: 18px;
    color: #fff;
    border:none;
    cursor:pointer;
    transition: background-color 0.3s;
}
.generate-button{
    background-color: #5228c6;
    transition: 0.5s;
}
.generate-button:hover{
    background-color: #3100b6;
}
.generate-button:disabled{
    background-color:#bdc3c7;
}
.download-button{
    background-color: #27ae60;
    margin-left:10px;
    transition: 0.5s;
}
.download-button:hover{
    background-color: #219a52;
}
.app-container h1{
    font-weight: 600;
    font-size: 18px;
    color: #3100b6;
    border-bottom: 1px solid #f1efef;
    padding:10px 0px;
}
.footer{
    margin-top:10px;
}
.footer a{
    text-decoration: none;
    color:#3100b6;
}